<template>
<div class="content-body">
    <div class="column box-max">

    <div class="columns " >
      <div class="column box ">
        <div class="container" style="margin-left: 5px; padding-bottom: 1.5rem">
            <div class="title" style="margin-bottom: 0px">{{ $t('login.resetPassword') }}</div>
            <div style="color: #95989A;margin-bottom: 24px; margin-top: 12px">{{ $t('login.resetPassword') }} ? </div>

                <b-field
                  :type="{ 'is-danger': false, 'is-success': false }"
                  message=" ">
                    <b-input type="email"
                      use-html5-validation
                      v-model="user.email"
                      :placeholder="$t('login.email')"
                      size="is-large" expanded 
                      maxlength="30">
                    </b-input>
                </b-field>

               <br/>
               <b-button type="is-primary" size="is-large" expanded @click="handleLogin()">{{ $t('login.sendResetLink') }}</b-button>

          
          <b-loading :is-full-page="isFullPage" :active.sync="loading" :can-cancel="true">
              <b-icon
                    pack="fas"
                    icon="spinner"
                    size="is-large"
                    custom-class="fa-spin">
              </b-icon>
          </b-loading>
 
        </div>
      </div>
    </div>



    </div>

</div>



</template>

<script>
  //import { ValidationObserver, ValidationProvider } from "vee-validate"
  import * as EmailValidator from 'email-validator'
  import User from '../model/user'
  import Notification from '../utils/notification'

  export default {
    components: {
      //ValidationObserver,
      //ValidationProvider
    },
    data: function() {
      return {
        user: new User('', ''), // for login
        activeTab: 0,
        loading: false,
        email: "",
        password: "",
        confirmation: "",
        errorInfo: "",
        regErrorInfo: "",
        isFullPage: true
      }
    },
    computed: {
      loggedIn() {
        return this.$store.state.auth.status.loggedIn
      }
    },
    created() {
      if (this.loggedIn) {
        this.$router.push('/search')
      }
    },
    mounted () {
      //this.activeTab = 0 //parseInt(this.$route.query.ref) - 1
    },
    methods: {
      handleLogin() {
        //Notification.showSuccess(this, this.$t('error.registerSuccessful'))
        //Notification.showError(this, this.$t('error.registerSuccessful'))
        //return;

        var isEmailValid = EmailValidator.validate(this.user.email)
        var isPasswordValid = this.user.password.length >= 8

        if (isEmailValid && isPasswordValid) {
          this.loading = true

          this.$store.dispatch('auth/login', this.user).then(
            () => {
              this.$router.push('/search')
            },
            error => {
              this.loading = false
              this.errorInfo = this.$t('error.emailOrPasswordError')
            }
          )
        } else {
          if (!isEmailValid) {
            this.errorInfo = this.$t('error.emailFormartError')
          } else {
            this.errorInfo = this.$t('error.passwordLengthError')
          }
          
          return
        }
      },
      handleRegister() {
        var isEmailValid = EmailValidator.validate(this.email)
        var isPasswordValid = this.password.length >= 8

        if (!isEmailValid) {
          this.regErrorInfo = this.$t('error.emailFormartError')
          return
        }

        if (!isPasswordValid) {
          this.regErrorInfo = this.$t('error.passwordLengthError')
          return
        }

        if (this.password && this.confirmation) {
          if (this.password != this.confirmation) {
            this.regErrorInfo = this.$t('error.passwordRepeatError')
            return
          } else {  // handle register
            this.loading = true

            var user = new User(this.email, this.password)
            this.$store.dispatch('auth/register', user).then(
              data => {
                //this.regErrorInfo = this.$t('error.registerSuccessful')
                Notification.showSuccess(this, this.$t('error.registerSuccessful'))
                this.loading = false
                this.activeTab = 0
              },
              error => {
                this.loading = false
                this.regErrorInfo = this.$t('error.registerError')
              }
            )
          }

        } else {
          this.regErrorInfo = this.$t('error.passwordRepeatError')
          return
        }
      },
      showSuccess(str) {
        const notif = this.$buefy.notification.open({
          duration: 5000,
          message: str,
          position: 'is-bottom-right',
          type: 'is-success',
          hasIcon: false
        })
      }
    }
  }
</script>

