import axios from 'axios';
import { AUTH_API_URL } from '../commons/api'

//const API_URL = 'http://localhost:8080/api/auth/';
const API_URL = AUTH_API_URL;
console.log(API_URL)

class AuthService {
  login(user) {
    return axios
      .post(API_URL + '/signin', {
        //username: user.username,
        email: user.email,
        password: user.password
      })
      .then(response => {
        if (response.data.accessToken) {
          localStorage.setItem('user', JSON.stringify(response.data));
        }

        return response.data;
      });
  }

  logout() {
    localStorage.removeItem('user');
  }

  register(user) {
    return axios.post(API_URL + '/signup', {
      //username: user.username,
      email: user.email,
      password: user.password,
      roles: ['user']
    });
  }

  resetPassword(user) {
    return axios.post(API_URL + '/resetpassword', {
      email: user.email,
      password: user.password,
      newPassword: user.newPassword,
      accessToken: user.accessToken
    });
  }

  verifyEmail(params) {
    return axios.post(API_URL + '/emailVerification', {
      email: params.email,
      accessToken: params.accessToken
    });
  }

  confirmVerifyEmail(params) {
    return axios.get(API_URL + '/confirmVerification?token=' + params.token + "&email=" + params.email)
    //return axios.get(API_URL + '/confirmVerification', {
    //  email: params.email,
    //  token: params.token
    //});
  }
}

export default new AuthService();
