<script>
  import china from './../assets/svg/flags/china.svg'
  import usa from './../assets/svg/flags/usa.svg'
  import dropdown from './../assets/svg/drop-down.svg'

  export default {
    data: function() {
      return {
        china: china,
        usa: usa,
        dropdown: dropdown,

        isChinese: false,
        active: false,
        options: [{
          value: '0',
          label: '简体中文',
          type: 'cn'
        }, {
          value: '1',
          label: 'English',
          type: 'en'
        },],
        value: '0',
      }
    },
    created() {
      if (this.locale == 'en-US') {
        //this.value = '1'
        this.isChinese = false
      } else {
        //this.value = '0'
        this.isChinese = true
      }
    },
    computed: {
      locale() {
        return this.$store.state.language.lang.locale
      }
    },
    methods: {
      test() {
        console.log(this.value)
        console.log(this.isChinese)
      },
      toggleLang(lang) {

        var newLang = null

        if (lang == 'zh') {
          newLang = {
            locale: 'zh-CN'
          }
          //this.value = '0'
          this.isChinese = true

          document.title = "Trust Domains - 可信去中心化金融生态"
        } else if (lang == 'en') {
          newLang = {
            locale: 'en-US'
          }
          //this.value = '1'
          this.isChinese = false

          document.title = "Trust Domains - For Trust Decentralized Finance Economy"
        }

        localStorage.setItem('lang', JSON.stringify(newLang))

        this.$i18n.locale = newLang.locale
        this.$store.dispatch('language/updateLang', newLang)
      },
      //randomTheme: function() {
      //   this.selectedNavTheme = this.navbarThemes[Math.floor(Math.random() * this.navbarThemes.length)]
      //}
    }
  }


</script>

