
class Notification {
    showSuccess (that, str) {
        const notif = that.$buefy.notification.open({
          duration: 3000,
          message: str,
          position: 'is-bottom-right',
          type: 'is-success',
          hasIcon: false
        })
    }

    showError (that, str) {
        const notif = that.$buefy.notification.open({
          duration: 3000,
          message: str,
          position: 'is-bottom-right',
          type: 'is-danger',
          hasIcon: false
        })
    }
}

export default new Notification()