<template>
  <div>
    <section class="hero is-fullheight">

      <div class="hero-body">
        <div class="container has-text-centered">

          <div class="column is-6 is-offset-3">
            <h1 class="title white">
                {{ info }}
            </h1>
          </div>

        </div>

      </div>


    </section>
  </div>
</template>

<script>

  export default {
    data: function() {
      return {
        loading: false,
        info: ''
      }
    },
    created() {

      if (this.isVerified) {
        this.info = this.$t('info.isVerified')
        this.naviToSearch()
      } else {
        this.loading = true

        var params = {
          "email": this.$route.query.email,
          "token": this.$route.query.token
        }

        this.$store.dispatch('auth/confirmVerifyEmail', params).then(
          data => {
            this.info = this.$t('info.isVerified')

            var user = JSON.parse(localStorage.getItem('user'))
            user.isVerified = true

            this.$store.dispatch('auth/updateUser', user)

            this.loading = false
            this.naviToSearch()
          },
          error => {
            this.info = this.$t('info.isVerifiedError')
            this.loading = false
          }
        )
      }
    },
    computed: {
      isVerified() {
        return this.$store.state.auth.user.isVerified
      }
    },
    methods: {
      naviToSearch() {
        setTimeout(() => {
          this.$router.push('/search')
        }, 2000)
      }
    }
  }
</script>

<style scoped>

  .hero.is-info {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
  }


</style>
